@charset "UTF-8";
/* only */
@keyframes flash {
  from {
    opacity: 1; }
  to {
    opacity: 0; } }

@keyframes spin {
  0% {
    transform: scale3d(0.7, 0.7, 1); }
  100% {
    transform: scale3d(1, 1, 1); } }

.inn-ping__results {
  display: flex;
  align-items: center;
  background: #333;
  color: #ccc;
  border-radius: 0 0 1rem 1rem;
  padding: 0.5rem 1rem;
  border-top: 1px dashed rgba(255, 255, 255, 0.1);
  flex-wrap: wrap; }
  .inn-ping__results__times {
    flex: 0 0 100%; }
    @media (min-width: 992px) {
      .inn-ping__results__times {
        flex: 1 0 auto; } }

.inn-ping__btn {
  display: flex;
  align-items: center; }
  .inn-ping__btn::before {
    content: '';
    margin-right: 0.5rem; }
  .inn-ping__btn.is-active::before {
    animation: spin 0.5s linear infinite;
    content: ''; }

.inn-ping__item {
  flex: 0 0 100%; }
  @media (min-width: 579px) {
    .inn-ping__item {
      flex: 0 0 50%; } }
  @media (min-width: 768px) {
    .inn-ping__item {
      flex: 0 0 33.33333%; } }
  @media (min-width: 992px) {
    .inn-ping__item {
      flex: 0 0 25%; } }
  @media (min-width: 1200px) {
    .inn-ping__item {
      flex: 0 0 20%; } }
  .inn-ping__item:last-child:after {
    content: '_';
    color: #ccc;
    font-weight: bold;
    animation: flash .2s linear infinite; }
  .inn-ping__item__no {
    opacity: 0.5; }
  .inn-ping__item__line {
    opacity: 0.3; }
  .inn-ping__item__time {
    font-weight: bold; }
  .inn-ping__item__container {
    display: flex;
    flex-wrap: wrap;
    list-style-type: none;
    background: #333;
    color: #ccc;
    padding: 0.5rem 1rem;
    margin: 0.5rem 0 0;
    max-height: 8rem;
    overflow-y: auto;
    border-radius: 1rem 1rem 0 0;
    box-shadow: inset 0 10px 10px rgba(0, 0, 0, 0.1); }
